
body,
html,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li {
  margin: 0;
  padding: 0
}
body,
html {
  height: 100%;
  background: #eff3f2;
}
ul,
ol,
li {
  list-style: none;
}
a {
  text-decoration: none;
}
img {
  vertical-align: top;
}
input {
  outline: none;
  border: none;
}
::-webkit-scrollbar {
  width: 0px
}
body,
#app {
  height: 100%;
  min-width: 320px;
  max-width: 720px;
  margin: 0 auto;
  position: relative;
}
#view {
  position: absolute;
  top: 1.5555555555555556rem;
  bottom: 1.3611111111111112rem;
  left: 0;
  right: 0;
  overflow-y: auto;
}



